<template>
  <div>
    <div v-if="cartList.length">

      <div class="text-right">
        <button @click="checkout" class="w-full bg-indigo-700 hover:bg-indigo-500 text-white font-bold py-2 px-4 rounded">
          Checkout
        </button>

        <h1 class="text-2xl mt-5 flex justify-between">
          <div class="text-gray-600">Total</div>
          <div class="text-white text-bold">{{ totalAmount | money }}</div>
        </h1>
      </div>

      <div v-for="(cart, index) in cartList" :key="index" class="my-4">
        <div class="text-white p-4 bg-black flex justify-between rounded-t">
          <div>
            {{ cart.name }}
          </div>
        </div>
        <div class="text-black bg-white font-bold py-2 px-4 flex justify-between">
          <div>
            <span class="text-gray-600">{{ cart.price | money }}</span> &times; {{ cart.qty }}
          </div>
          <div>
            {{ cart.price * cart.qty | money }}
          </div>
        </div>
        <div class="flex justify-between bg-indigo-900 p-4 rounded-b">
          <button @click="removeFromCart(cart)" class="text-white hover:bg-indigo-600 rounded px-4 py-2">Remove</button>
          <div>
            <button @click="increaseItem(cart)" class="text-white bg-indigo-700 hover:bg-indigo-600 rounded px-4 text-2xl mr-4">+</button>
            <button @click="decreaseItem(cart)" class="text-white bg-indigo-700 hover:bg-indigo-600 rounded px-4 text-2xl">-</button>
          </div>
        </div>
      </div>
    </div>

    <div v-if="!cartList.length" class="text-center text-white">
      There's no item in the cart
    </div>
  </div>
</template>

<script>
  import { mapState, mapGetters, mapActions } from 'vuex'

  export default {
    name: "Cart.vue",

    components: {
      Product: () => import('../components/Product')
    },

    props: {
      carts: {
        type: Array
      }
    },

    computed: {
      ...mapState(['cartList']),
      ...mapGetters(['totalAmount']),
    },

    methods: {
      ...mapActions([
        'increaseItem',
        'decreaseItem',
        'removeFromCart',
      ]),

      checkout() {
        this.$router.push('/checkout')
      }
    }
  }
</script>

